// 
export let lineOption = {
  backgroundColor: "#fff",
  color: ["#5f9ea0", "#efcf73", "#8ec9eb"],
  title: {},
  tooltip: {
    trigger: "axis"
  },
  toolbox: {
    right: '4%',
    feature: {
      dataView: {},
      saveAsImage: {},
      restore: {},
      magicType: {
        type: ['line', 'bar', 'stack', 'tiled']
      }
    }
  },
  legend: {
    data: [{
      name: "",
      icon: "circle"
    }, {
      name: "",
      icon: "circle"
    }, {
      name: "",
      icon: "circle"
    }],
    left: "5%",
    top: '3%'
  },
  grid: {
    left: "6%",
    right: "5%",
    bottom: '10%',
    top: "12%"
  },
  xAxis: {
    type: "category",
    boundaryGap: false,
    data: [],
    axisLine: {
      lineStyle: {
        color: "#999"
      }
    },
    splitLine: {
      show: true,
      lineStyle: {
        type: "dotted",
        color: "#eee"
      }
    }
  },
  yAxis: [
    {
      type: "value",
      axisLabel: {
        formatter: "{value}/"
      },
      axisLine: {
        lineStyle: {
          color: "#909090"
        }
      },
      splitLine: {
        show: true,
        lineStyle: {
          type: "dotted",
          color: "#eee"
        }
      }
    },
    {
      type: "value",
      axisLabel: {
        formatter: "{value} %"
      },
      axisLine: {
        lineStyle: {
          color: "#909090"
        }
      }
    }
  ],
  series: [
    {
      name: "",
      type: "line",
      smooth: true,
      data: [

      ],
      markPoint: {
        data: [
          { type: "max", name: "" },
          { type: "min", name: "" }
        ]
      },
      markLine: {
        data: [{ type: "average", name: "" }]
      }
    },
    {
      name: "",
      type: "line",
      smooth: true,
      yAxisIndex: 1,
      data: [

      ],
      markPoint: {
        data: [{ name: "", value: -2, xAxis: 1, yAxis: -1.5 }]
      },
      markLine: {
        data: [
          { type: "average", name: "" },
          [
            {
              symbol: "none",
              x: "90%",
              yAxis: "max"
            },
            {
              symbol: "circle",
              label: {
                position: "start",
                formatter: ""
              },
              type: "max",
              name: ""
            }
          ]
        ]
      }
    }, {
      name: "",
      type: "line",
      smooth: true,
      data: [

      ],
      markPoint: {
        data: [
          { type: "max", name: "" },
          { type: "min", name: "" }
        ]
      },
      markLine: {
        data: [{ type: "average", name: "" }]
      }
    },
  ]
};
// 
export let barOpiton = {
  backgroundColor: "#fff",
  color: ["#5f9ea0", "#efcf73", "#8ec9eb"],
  legend: {
    data: [{
      name: "",
      icon: "diamond"
    }, {
      name: "",
      icon: "diamond"
    }, {
      name: "",
      icon: "diamond"
    }],
    left: "5%",
    top: '3%'
  },
  tooltip: {
    trigger: "axis"
  },
  grid: {
    left: "6%",
    right: "5%",
    bottom: '10%',
    top: "12%"
  },
  toolbox: {
    right: '4%',
    feature: {
      dataView: {},
      saveAsImage: {},
      restore: {},
      magicType: {
        type: ['line', 'bar', 'stack', 'tiled']
      }
    }
  },
  xAxis: {
    type: 'category',
    data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
    axisLine: {
      lineStyle: {
        color: "#999"
      }
    },
    splitLine: {
      show: true,
      lineStyle: {
        type: "dotted",
        color: "#eee"
      }
    }
  },
  yAxis: [{
    type: 'value',
    axisLine: {
      lineStyle: {
        color: "#909090"
      }
    },
    splitLine: {
      show: true,
      lineStyle: {
        type: "dotted",
        color: "#eee"
      }
    }
  }, {
    type: 'value',
    axisLine: {
      lineStyle: {
        color: "#909090"
      }
    },
    splitLine: {
      show: true,
      lineStyle: {
        type: "dotted",
        color: "#eee"
      }
    }
  }],
  series: [{
    data: [],
    type: 'bar',
    name: "",
  }, {
    data: [],
    type: 'bar',
    yAxisIndex: 1,
    name: "",
  }, {
    data: [],
    type: 'bar',
    name: "",
  }]
};

export let pieOption = {
  tooltip: {
    trigger: "item",
    formatter: "{a} <br/>{b} : {c} ({d}%)"
  },
  legend: {
    left: "center",
    top: "bottom",
    data: [
      "",
      "",
      ""
    ]
  },
  toolbox: {
    show: true,
    feature: {
      mark: { show: true },
      dataView: { show: true, readOnly: false },
      magicType: {
        show: true,
        type: ["pie", "funnel"]
      },
      restore: { show: true },
      saveAsImage: { show: true }
    }
  },
  series: [
    {
      name: "",
      type: "pie",
      radius: [30, 110],
      roseType: "area",
      data: [
        { value: 10, name: "rose1" },
        { value: 5, name: "rose2" },
        { value: 15, name: "rose3" },
        { value: 25, name: "rose4" },
        { value: 20, name: "rose5" },
        { value: 35, name: "rose6" },
        { value: 30, name: "rose7" },
        { value: 40, name: "rose8" }
      ]
    }
  ]
};



